import React, { Fragment } from 'react';
import {
  Col, Container, Navbar, Row,
} from 'react-bootstrap';
import '../styles/footer.css';
import ContentLine from './contentLine';


const style = {
  padding: '7rem 0',
  backgroundColor: '#e9ecef',
};


function BottomText({ text }) {
  return (
    <ul className="footer-list">
      <ContentLine />
      {
        text.map(el => (
          <li>
            {
              el.href ? <a href={el.href}>{el.text}</a> : (
                <Fragment>
                  {' '}
                  {el.text}
                  {' '}
                </Fragment>
              )
            }
          </li>
        ))
      }
    </ul>
  );
}

export default ({ footerData }) => (
  <Navbar sticky="bottom" style={style}>
    <Container>
      <Row>
        {
          footerData.map(el => (
            <Col>
              <h4>{el.name}</h4>
              <BottomText text={el.text} />
            </Col>
          ))
        }
      </Row>
    </Container>
  </Navbar>
);
