/* eslint-disable react/destructuring-assignment,react/prop-types,max-len */
import React from 'react';
import { Navbar, Nav, Image } from 'react-bootstrap';
import { Link } from 'gatsby';
import 'bootstrap/dist/css/bootstrap.min.css';

const SwitchableNavbar = ({
  img, headerText, menuButtons, color,
}) => (
  <Navbar collapseOnSelect expand="lg" fixed="top" style={{ backgroundColor: color }}>
    <Navbar.Brand href="/">
      <Image
        src={img}
        width="60"
        height="60"
        alt="React Bootstrap logo"
        rounded
        fluid
      />
    </Navbar.Brand>
    <Navbar.Brand href="/">
      {headerText}
    </Navbar.Brand>
    <Navbar.Toggle />
    <Navbar.Collapse className="justify-content-end">
      <Nav>
        {
          menuButtons.map(el => (
            <Link className="nav-link" to={el.href}>
              {el.name}
            </Link>
          ))
        }
      </Nav>
    </Navbar.Collapse>
  </Navbar>
);

export default class CustomMenu extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isTop: true,
      color: '',
    };
  }

  componentDidMount() {
    document.addEventListener('scroll', () => {
      const isTop = window.scrollY < 100;
      if (isTop !== this.state.isTop) {
        this.setState({ isTop });
        if (!this.state.isTop) {
          this.setState({ color: 'white' });
        } else {
          this.setState({ color: '' });
        }
      }
    });
  }

  render() {
    return (
      <SwitchableNavbar color={this.state.color} img={this.props.img} headerText={this.props.headerText} menuButtons={this.props.menuButtons} />
    );
  }
}
